/* Default styles for larger screens */
.navbar {
  align-items: center;
  background-color: #70259b;
  display: flex;
  justify-content: space-between;
  padding: 8px 20px;
  position: relative;

  max-width: 100%; /* Added to make the navbar responsive */
}

.navbar .logo {
  height: 30.94px;
  position: relative;
  width: 100px;
}

.navbar .navbar-menu {
  align-items: flex-start;
  display: flex;
  position: relative;
  width: 649px;
}

/* Styles for smaller screens (e.g., mobile devices) */
@media (max-width: 768px) {
  .desktop .MENU
  {
    height: 120px !important;

  }
  .navbar {
    flex-direction: column; /* Stack items vertically */
    align-items: flex-start;
    padding: 8px;
    width: 100% !important;
  }

  .navbar .logo {
    width: 80px; /* Adjust the logo size for smaller screens */
    margin: 0px auto;
  }

  .navbar .navbar-menu {
    width: 100%; /* Occupy the full width of the screen */
  }

  .navbar .btn-primary-instance {
    margin: 0px auto;
  }
  .desktop .navbar-instance {
    height: 120px !important;
  }
}

.navbar .btn-primary-instance {
  flex: 0 0 auto !important;
}

.navbar .design-component-instance-node {
  font-family: var(--body-bold-14-font-family) !important;
  font-size: var(--body-bold-14-font-size) !important;
  font-style: var(--body-bold-14-font-style) !important;
  font-weight: var(--body-bold-14-font-weight) !important;
  letter-spacing: var(--body-bold-14-letter-spacing) !important;
  line-height: var(--body-bold-14-line-height) !important;
}
