.card-square {
  align-items: flex-start;
  background-color: var(--shadeswhite);
  box-shadow: 0px 8px 16px #3636361a;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 432px;
}

.card-square .card-image {
  align-items: flex-start;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 432px;
  position: relative;
  width: 100%;
}

.card-square .element {
  align-self: stretch;
  flex: 1;
  flex-grow: 1;
  object-fit: cover;
  position: relative;
  width: 100%;
}

.card-square .card-content {
  align-items: flex-start;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.64) 100%);
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: flex-end;
  left: 0;
  padding: 24px;
  position: absolute;
  top: 262px;
  width: 432px;
}

.card-square .rectangle {
  background-color: var(--primaryactive);
  height: 4px;
  position: relative;
  width: 40px;
}

.card-square .card-title {
  color: var(--shadeswhite);
  font-family: var(--heading-is-3-font-family);
  font-size: var(--heading-is-3-font-size);
  font-style: var(--heading-is-3-font-style);
  font-weight: var(--heading-is-3-font-weight);
  letter-spacing: var(--heading-is-3-letter-spacing);
  line-height: var(--heading-is-3-line-height);
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.card-square .lorem-ipsum-dolor {
  align-self: stretch;
  color: var(--shadeswhite);
  font-family: "Inter", Helvetica;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 24px;
  position: relative;
}
